import React from "react";
import "./IbbLegend.css";

const IbbLegend = () => {
  return (
    <div className="ibb">
      <p>
        Zemin davran analizlerinden bulunan ortalama spektral ivme deerlerine
        kar gelen blgeler (AS, BS, ve CS) ile Borchret (1994) bants
        kullanlarak edeer kayma dalgas hzndan hesaplanan spektral ivmelere
        kar gelen blgeler (AV, BV ve CV) arasnda bir deerlendirme
        yaplmtr.
        <br />
        Bu iki harita  blgeye ayrld iin, sonu rn de yine  farkl
        blgeye aadaki kriterlere uygun olarak ayrlmtr.
        <br />
        <strong>Ags Blgesi</strong>, (AS ile AV) veya (AS ile BV) veya (BS ile
        AV) blgelerinin rtt blgelerdir.
        <br />
        <strong>Bgs blgesi</strong>, (AS ile CV) veya (CS ile AV) veya (BS ile
        BV) blgelerinin rtt blgelerdir.
        <br />
        <strong>Cgs blgesi</strong>, (BS ile CV) veya (CS ile BV) veya (BS ile
        CV) blgelerinin rtt blgelerdir.
      </p>
    </div>
  );
};

export default IbbLegend;
