import React from "react";
import "./HoverMenu.css";

const HoverMenu = ({ hoverInfo }) => {
  return (
    <div className="tooltip" style={{ left: hoverInfo.x, top: hoverInfo.y }}>
      <div>{hoverInfo.feature.properties.adi}</div>
      <br />
      <div className="tooltip-properties">
        <div className="tooltip-properties-human">
          <div>Can Kayb :{hoverInfo.feature.properties.can_kaybi}</div>
          <div>Ar Yaral :{hoverInfo.feature.properties.agir_yarali}</div>
          <div>Hafif Yaral :{hoverInfo.feature.properties.hafif_yarali}</div>
          <div>
            Hastanede Tedavi Edilecek Yaral :
            {hoverInfo.feature.properties.hastane_tedavi_yarali}
          </div>
          <div>
            Barnma htiyac Olacak Hane :
            {hoverInfo.feature.properties.barinma_ihtiyaci_hane_sayisi}
          </div>
        </div>

        <div className="tooltip-properties-building">
          <div>
            ok Ar Hasarl Bina :
            {hoverInfo.feature.properties.bina_cok_agir_hasar}
          </div>
          <div>
            Ar Hasarl Bina :{hoverInfo.feature.properties.bina_agir_hasar}
          </div>

          <div>
            Orta Hasarl Bina :{hoverInfo.feature.properties.bina_orta_hasar}
          </div>
          <div>
            Hafif Hasarl Bina :{hoverInfo.feature.properties.bina_hafif_hasar}
          </div>
        </div>
      </div>
    </div>
  );
};

export default HoverMenu;
