import { React } from "react";
import { Accordion } from "react-bootstrap";
import * as BsIcons from "react-icons/bs";
import "./AboutMap.css";

function AboutMap() {
  return (
    <Accordion.Item eventKey="1">
      <Accordion.Header>
        <div className="accordion-header-desc">
          <BsIcons.BsFillMapFill />
          Harita Hakknda
        </div>
      </Accordion.Header>
      <Accordion.Body>
        <div>
          <p className="ibb-map-description">
            stanbul Anadolu Yakas yer sarsnt haritas stanbul Bykehir
            Belediyesi Deprem ve Zemin nceleme Mdrl'nn Anadolu Yakas
            (2009) Mikroblgeleme Projeleri kapsamnda retilmitir. altay
            sonras retilen harita, tekrar projekte edilerek uygulama
            oluturulmutur.
          </p>
          <p className="ibb-map-description">
            Harita zerinde gezinerek 7.5 iddetindeki deprem senaryosundaki
            olas can ve mal kayplar grntlenebilir.
          </p>
        </div>
      </Accordion.Body>
    </Accordion.Item>
  );
}

export default AboutMap;
